@import "vars";

body {
	font-family: 'Gothic';
	font-size: 14px;
	line-height: 1.85;
	position: relative; }

.preloader {
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	position: fixed;
	z-index: 100;
	&__in {
		background-image: radial-gradient(370.00px at 50% 50%, #195B05 0%, #003820 100%);
		background-size: cover;
		height: 100%;
		width: 100%; }
	&__center {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		text-align: center;
		width: 100%; }
	&__logo {
		animation: perfectGropSlideTop 3s forwards;
		// opacity: 0
		svg {
			width: 100%; } }
	&__number {
		color: #E4F3DD;
		margin-top: 30px;
		font-family: 'Gothic-Bold'; } }
.letter-e {
  animation: perfectGropSlideRight 2s forwards;
  opacity: 0; }
.letter-g {
	animation: perfectGropSlideLeft 2s forwards;
	opacity: 0; }
.letter {
	animation: perfectGropSlideCenter 3s forwards;
	opacity: 0; }
@keyframes perfectGropSlideCenter {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }
@keyframes perfectGropSlideRight {
  from {
    opacity: 0;
    transform: translateX(-100%); }
  to {
    opacity: 1;
    transform: translateX(0%); } }
@keyframes perfectGropSlideLeft {
  from {
    opacity: 0;
    transform: translateX(100%); }
  to {
    opacity: 1;
    transform: translateX(0%); } }

