@import url(libs/slick.css);
@font-face {
  font-family: "Gothic";
  font-weight: normal;
  font-style: normal;
  src: url("../fonts/CenturyGothic.eot");
  src: url("../fonts/CenturyGothic.eot?#iefix") format("embedded-opentype"), url("../fonts/CenturyGothic.woff") format("woff"), url("../fonts/CenturyGothic.ttf") format("truetype");
}

@font-face {
  font-family: "Gothic-Bold";
  font-weight: bold;
  font-style: normal;
  src: url("../fonts/CenturyGothic-Bold.eot");
  src: url("../fonts/CenturyGothic-Bold.eot?#iefix") format("embedded-opentype"), url("../fonts/CenturyGothic-Bold.woff") format("woff"), url("../fonts/CenturyGothic-Bold.ttf") format("truetype");
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  box-sizing: border-box;
}

body {
  font-family: 'Gothic';
  font-size: 14px;
  line-height: 1.85;
  position: relative;
}

.preloader {
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  position: fixed;
  z-index: 100;
}

.preloader__in {
  background-image: radial-gradient(370px at 50% 50%, #195B05 0%, #003820 100%);
  background-size: cover;
  height: 100%;
  width: 100%;
}

.preloader__center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  width: 100%;
}

.preloader__logo {
  animation: perfectGropSlideTop 3s forwards;
}

.preloader__logo svg {
  width: 100%;
}

.preloader__number {
  color: #E4F3DD;
  margin-top: 30px;
  font-family: 'Gothic-Bold';
}

.letter-e {
  animation: perfectGropSlideRight 2s forwards;
  opacity: 0;
}

.letter-g {
  animation: perfectGropSlideLeft 2s forwards;
  opacity: 0;
}

.letter {
  animation: perfectGropSlideCenter 3s forwards;
  opacity: 0;
}

@keyframes perfectGropSlideCenter {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes perfectGropSlideRight {
  from {
    opacity: 0;
    transform: translateX(-100%);
  }
  to {
    opacity: 1;
    transform: translateX(0%);
  }
}

@keyframes perfectGropSlideLeft {
  from {
    opacity: 0;
    transform: translateX(100%);
  }
  to {
    opacity: 1;
    transform: translateX(0%);
  }
}

